.login-page {
  $bg: #EDF2F0;

  $neu-1: #ecf0f3;
  $neu-2: #d1d9e6;

  $white: #f9f9f9;
  $gray: #a0a5a8;
  $black: #181818;

  $purple: #4B70E2;


  $transition: 1.25s;

  *, *::after, *::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    user-select: none;
  }
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Montserrat', sans-serif;
  font-size: 12px;
  background-color: $neu-1;
  color: $gray;
  
  /**/
  .main{
      position: relative;
      width: 1000px;
    min-width: 1000px;
    min-height: 600px;
      height: 600px;
      padding: 25px;
    background-color: $neu-1;
    box-shadow:
      10px 10px 10px $neu-2,
      -10px -10px 10px $white;
    border-radius: 12px;
    overflow: hidden;
    @media(max-width: 1200px){  transform: scale(.7); }
    @media(max-width: 1000px){  transform: scale(.6); }
    @media(max-width: 800px){  transform: scale(.5); }
    @media(max-width: 600px){  transform: scale(.4); }
    

  }
  .container{
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;

      top: 0;
      width: 600px;
      height: 100%;
      padding: 25px;

    background-color: $neu-1;
      transition: $transition;
  }
  .form{
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      width: 100%;
      height: 100%;

      &__icon{
          object-fit: contain;
          width: 30px;
          margin: 0 5px;
          opacity: .5;
          transition: .15s;

          &:hover{
              opacity: 1;
              transition: .15s;
              cursor: pointer;

          }
      }
      &__input{
          width: 350px;
          height: 40px;
          margin: 4px 0;

          padding-left: 25px;
          font-size: 13px;
          letter-spacing: .15px;
          border: none;
          outline: none;
          color: $black; // 텍스트 색상을 더 진한 색으로 변경


          font-family: 'Montserrat', sans-serif;
      background-color: $neu-1;
      transition: .25s ease;
      border-radius: 8px;

      box-shadow:
        inset 2px 2px 4px $neu-2,
        inset -2px -2px 4px $white;

      &:focus{
        box-shadow:
          inset 4px 4px 4px $neu-2,
          inset -4px -4px 4px $white;
      }
      }
      &__span{
          margin-top: 30px;
          margin-bottom: 12px;
      }
      &__link{
          color: $black;
          font-size: 15px;
          margin-top: 25px;
          // border-bottom: 1px solid $gray;
          line-height: 2;
      }
  }
  .title{
      font-size: 34px;
      font-weight: 700;
      line-height: 3;
      color: $black;
  }
  .description{
      font-size: 14px;
      letter-spacing: .25px;
      text-align: center;
      line-height: 1.6;
  }
  .button{
      width: 180px;
      height: 50px;
      border-radius: 25px;
      margin-top: 50px;
      font-weight: 700;
      font-size: 14px;
      letter-spacing: 1.15px;

    background-color: $purple;
    color: $white;
    box-shadow:
      8px 8px 16px $neu-2,
      -8px -8px 16px $white;

    border: none;
      outline: none;
  }
  /**/

  .a-container{
      z-index: 100;
      left: calc(100% - 600px );
  }
  .b-container{
      left: calc(100% - 600px );
      z-index: 0;
  }

  .switch{
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 400px;

      padding: 50px;
      z-index: 200;
      transition: $transition;

      background-color: $neu-1;
      overflow: hidden;


    box-shadow:
      4px 4px 10px $neu-2,
      -4px -4px 10px $white;

      &__circle{
          position: absolute;
          width: 500px;
          height: 500px;
          border-radius: 50%;
      background-color: $neu-1;
      box-shadow:
        inset 8px 8px 12px $neu-2,
        inset -8px -8px 12px $white;

          bottom: -60%;
          left: -60%;
          transition: $transition;

          &--t{
              top: -30%;
              left: 60%;
              width: 300px;
              height: 300px;
          }
      }

      &__container{
          display: flex;
          justify-content: center;
          align-items: center;
          flex-direction: column;
          position: absolute;
          width: 400px;
          padding: 50px 55px;

          transition: $transition ;

      }

      &__button{
          cursor: pointer;
      &:hover{
        box-shadow:
          6px 6px 10px $neu-2,
          -6px -6px 10px $white;
        transform: scale(.985);
        transition: .25s;
      }
      &:active,
      &:focus{
        box-shadow:
          2px 2px 6px $neu-2,
          -2px -2px 6px $white;
        transform: scale(.97);
        transition: .25s;
      }
      }
  }
  /**/
  .is-txr{
      left: calc(100% - 400px );
      transition: $transition;
      transform-origin: left;
  }
  .is-txl{
      left: 0;
      transition: $transition;
      transform-origin: right;
  }
  .is-z200{
      z-index: 200;
      transition: $transition;
  }
  .is-hidden{
      visibility: hidden;
      opacity: 0;
      position: absolute;
      transition: $transition;
  }
  .is-gx{
      animation: is-gx $transition;
  }
  @keyframes is-gx {
      0%, 10%, 100%{ width: 400px; }
      30%, 50%{ width: 500px;}
  }
}